<template>
  <div>
    <div class="banner" @click="handleBannerClick">
      <img :src="bannerImg" class="banner-img">
      <div class="banner-info">
        <div class="banner-title">{{this.sightName}}</div>
        <div class="banner-number"><span class="iconfont banner-icon">&#xe600;</span>{{this.gallaryImgs.length}}</div>
      </div>
    </div>
    <fade>
      <common-gallary
        :imgs="gallaryImgs"
        v-show="showGallary"
        @close="handleGallaryClose"
      >
      </common-gallary>
    </fade>
  </div>
</template>

<script>
import commonGallary from 'common/gallary/gallary.vue'
import fade from 'common/fade/fade.vue'
export default {
  name: 'Banner',
  props: {
    sightName: String,
    bannerImg: String,
    gallaryImgs: Array
  },
  components: {
    commonGallary,
    fade
  },
  data () {
    return {
      showGallary: false
    }
  },
  methods: {
    handleBannerClick () {
      this.showGallary = true
    },
    handleGallaryClose () {
      this.showGallary = false
    }
  }
}
</script>

<style lang="stylus" scoped>
  .banner
    position:relative
    overflow:hidden
    height:0
    padding-bottom:55%
    .banner-img
      width: 100%
    .banner-info
      color:#fff
      position: absolute
      left:0
      right:0
      bottom:0
      line-height:.6rem
      display:flex
      background-image: linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8))
      .banner-title
        font-size:.32rem
        padding:0 .2rem
        flex:1
      .banner-number
        padding:0 .4rem
        height:.32rem
        line-height:.32rem
        margin-top:.14rem
        border-radius:.2rem
        background:rgba(0, 0, 0, .8)
        font-size:.24rem
        .banner-icon
          font-size:.24rem
          margin-right: .08rem
</style>



